import React, { Component } from "react";
import CourseDataService from "./course.service";

export default class AddCourse extends Component {
    constructor(props) {
        super(props);
        this.onChangeId = this.onChangeId.bind(this);
        this.onChangeName = this.onChangeName.bind(this);
        this.onChangeTerm = this.onChangeTerm.bind(this);
        this.onChangeYear = this.onChangeYear.bind(this);
        this.onChangeProf = this.onChangeProf.bind(this);
        this.onChangeStart = this.onChangeStart.bind(this);
        this.onChangeEnd = this.onChangeEnd.bind(this);
        this.onChangeDay = this.onChangeDay.bind(this);
        this.saveCourse = this.saveCourse.bind(this);
        this.newCourse = this.newCourse.bind(this);

        this.state = {
            Course_id: "",
            Course_name: "",
            Course_term: "",
            Course_year: "",
            Prof_id: "",
            Course_start: "",
            Course_end: "",
            Course_day: "",

            submited: false
        };
    }

    onChangeId(e) {
        this.setState({
            Course_id: e.target.value
        });
    }

    onChangeName(e) {
        this.setState({
            Course_name: e.target.value
        });
    }

    onChangeTerm(e) {
        this.setState({
            Course_term: e.target.value
        });
    }

    onChangeYear(e) {
        this.setState({
            Course_year: e.target.value
        });
    }

    onChangeProf(e) {
        this.setState({
            Prof_id: e.target.value
        });
    }

    onChangeStart(e) {
        this.setState({
            Course_start: e.target.value
        });
    }

    onChangeEnd(e) {
        this.setState({
            Course_end: e.target.value
        });
    }

    onChangeDay(e) {
        this.setState({
            Course_day: e.target.value
        });
    }

    saveCourse() {
        var data = {
            Course_id: this.state.Course_id,
            Course_name: this.state.Course_name,
            Course_term: this.state.Course_term,
            Course_year: this.state.Course_year,
            Prof_id: this.state.Prof_id,
            Course_start: this.state.Course_start,
            Course_end: this.state.Course_end,
            Course_day: this.state.Course_day
        };

        CourseDataService.create(data)
            .then(response => {
                this.setState({
                    Course_id: response.data.Course_id,
                    Course_name: response.data.Course_name,
                    Course_term: response.data.course_term,
                    Course_year: response.data.course_year,
                    Prof_id: response.data.Prof_id,
                    Course_start: response.data.Course_start,
                    Course_end: response.data.Course_end,
                    Course_day: response.data.Course_day,

                    submitted: true
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    newCourse() {
        this.setState({
            Course_id: "",
            Course_name: "",
            Course_term: "",
            Course_year: "",
            Prof_id: "",
            Course_start: "",
            Course_end: "",
            Course_day: "",

            submited: false
        });
    }

    render() {
        return(
            <div className="submit-form">
                {this.state.submitted ? (
                    <div>
                        <h4>You submitted succesfully!</h4>
                        <button className="btn btn-success" onClick={this.newCourse}>
                            Add
                        </button>
                    </div>
                ) : (
                    <div>
                        <div className="form-group">
                            <label htmlFor="Course_id">Course ID</label>
                            <input
                                type="text"
                                className="form-control"
                                id="Course_id"
                                required
                                value={this.state.Course_id}
                                onChange={this.onChangeId}
                                name="Course_id"
                            />
                        </div>

                        <div className="form-group">
                            <label htmlFor="Course_name">Course Name</label>
                            <input
                                type="text"
                                className="form-control"
                                id="Course_name"
                                required
                                value={this.state.Course_name}
                                onChange={this.onChangeName}
                                name="Course_name"
                            />
                        </div>

                        <div className="form-group">
                            <label htmlFor="Course_term">Course Term</label>
                            <input
                                type="text"
                                className="form-control"
                                id="Course_term"
                                required
                                value={this.state.Course_term}
                                onChange={this.onChangeTerm}
                                name="Course_term"
                            />
                        </div>

                        <div className="form-group">
                            <label htmlFor="Course_year">Course Year</label>
                            <input
                                type="text"
                                className="form-control"
                                id="Course_year"
                                required
                                value={this.state.Course_year}
                                onChange={this.onChangeYear}
                                name="Course_year"
                            />
                        </div>

                        <div className="form-group">
                            <label htmlFor="Prof_id">Professor Id</label>
                            <input
                                type="text"
                                className="form-control"
                                id="Prof_id"
                                required
                                value={this.state.Prof_id}
                                onChange={this.onChangeProf}
                                name="Prof_id"
                            />
                        </div>

                        <div className="form-group">
                            <label htmlFor="Course_start">Course Start</label>
                            <input
                                type="text"
                                className="form-control"
                                id="Course_start"
                                required
                                value={this.state.Course_start}
                                onChange={this.onChangeStart}
                                name="Course_start"
                            />
                        </div>

                        <div className="form-group">
                            <label htmlFor="Course_end">Course End</label>
                            <input
                                type="text"
                                className="form-control"
                                id="Course_end"
                                required
                                value={this.state.Course_end}
                                onChange={this.onChangeEnd}
                                name="Course_end"
                            />
                        </div>

                        <div className="form-group">
                            <label htmlFor="Course_day">Course Day</label>
                            <input
                                type="text"
                                className="form-control"
                                id="Course_day"
                                required
                                value={this.state.Course_day}
                                onChange={this.onChangeDay}
                                name="Course_day"
                            />
                        </div>

                        <button onClick={this.saveCourse} className="btn btn-success">
                            Submit
                        </button>
                    </div>
                )}
            </div>
        );
    }
}